@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200&family=Comfortaa:wght@400;500;700&family=Hind+Madurai:wght@400;500;600;700&family=Karla:wght@400;500;600;700&family=Nunito:wght@400;500;600;700&family=Quicksand:wght@400;500;600;700&family=Rubik:wght@300;400;500;600;700&display=swap');

.loadingKeret{
  width:100%;
  height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(17, 22, 9);
}

.animation {
  height:95px;
  display: flex;
  transform: rotate(180deg);
}
.animation span{
  width: 38px;
  margin:0 2px;
  border-radius: 6px;
  animation: loader 2s infinite;
}

@keyframes loader {
  0%,
  100% {
    height: 15px;
    background: #0081c9;
  }
  25% {
    height: 95px;
    background: #ffb100;
  }
  50% {
    height: 50px;
    background: #54b435;
  }
  75% {
    height: 95px;
    background: #ff6464;
  }
}

.animation span:nth-child(1) {
  animation-delay: .2s;
}
.animation span:nth-child(2) {
  animation-delay: .4s;
}
.animation span:nth-child(3) {
  animation-delay: .6s;
}
.animation span:nth-child(4) {
  animation-delay: .8s;
}
.animation span:nth-child(5) {
  animation-delay: 1s;
}

